/*  Join Community Hero Banner Page
*
*  Version:
*    1.0.0 - 2022/01/06
*/

@import '../base.scss';

.c-join-community {

  &__inner {
    background-color: $s-color--background-tertiary;
    margin-bottom: 13.4375rem;
  }

  &__wrapper {
    padding-top: 8rem;
    padding-bottom: 2.5rem
  }

  &__background-image-container {
    display: none;
  }

  &__left-container {
    text-align: center;
    width: 83%;
    margin: 0 auto;
  }

  &__pretitle {
    margin-bottom: 1.375rem;
  }

  &__title {
    color: $s-color--font-6;
    margin-bottom: 1.3125rem;
  }

  &__text {
    opacity: 0.6;
    margin-bottom: 2.0625rem;
    max-width: 38.875rem;

    a{
      text-decoration: underline;
    }
  }

  &__text--product-promo {
    p {
      margin: revert;
    }
  }

  &__btn-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__btn-container {

    &:not(:last-child) {
      margin-right: 0.6875rem;
    }
  }

  &__btn {
    text-align: center;
    width: 8.875rem;

    &.o-btn.is-primary {
      padding: 0.625rem 0;
    }
  }

  &__slides {
    margin-bottom: -12.125rem;
    margin-top: 4.5625rem;
    margin-left: -3.75rem;
  }

  &__image-container {
    padding-bottom: 66.31%;
  }

  &__modal{
    &-message{
      position: fixed;
      top: 50%;
      left: 50%;
      transform: translate(-50% ,-50%);
      z-index: 200;
      background: rgb(255 255 255 / 90%);
      width: 100%;
      height: 100%;
      text-align: center;
      display: none;
      align-items: center;
      justify-content: center;
      flex-flow: column;
      padding: 2rem;
      &.is-visible{
        display: flex;
      }
    }
    &-btn{
      margin-top: 2rem;
    }
  }


  @media only screen and (min-width: 34.375rem) {
    &__slides {
      margin-left: 0;
    }

    &__image-container {
      padding-bottom: 42.31%;
    }
  }

  @include t-media-query($s-medium-up) {
    &__inner {
      position: relative;
      margin-bottom: 7.5rem;
    }

    &__background-image-container {
      display: block;
      height: 100%;
    }

    &__background-image-container-wrapper {
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      width: 43.5%;
    }

    &__wrapper {
      display: flex;
      justify-content: flex-end;
    }

    &__left-container {
      text-align: left;
      width: 38%;
      margin: 7.0625rem 5% 0 0;
    }

    &__pretitle {
      margin-bottom: 1.25rem;
    }

    &__title {
      margin-bottom: 1.25rem;
    }

    &__text {
      margin-bottom: 2.1875rem;
    }

    &__btn-wrapper {
      justify-content: flex-start;
    }

    &__slides {
      width: 50%;
      margin-bottom: 0;
    }

    &__image-container {
      padding-bottom: 66.70%;
    }

    &__btn {
      width: 10.8125rem;

      &.o-btn.is-primary,
      &.o-btn.is-tertiary {
        text-align: center;
        padding: 12px 0 16px;
      }
    }

    &__btn-container {

      &:not(:last-child) {
        margin-right: 1.25rem;
      }
    }
  }

  @include t-media-query($s-large-up) {
    &__wrapper {
      padding-top: 10.75rem;
      padding-bottom: 12.375rem;
    }

    &__pretitle {
      margin-bottom: 2.5rem;
    }

    &__title {
      margin-bottom: 2.5rem;
    }

    &__text {
      margin-bottom: 4.3125rem;
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__inner {
      margin-bottom: 8.6875rem;
    }
  }

  @include t-media-query($s-xxlarge-up) {

    &__wrapper {
      max-width: 125rem;
      margin: 0 auto;
    }
  }
}
